.floatingdiv {
  justify-content: space-around;
  display: flex;
  box-shadow: var(--boxShadow);
  background: white;
  border-radius: 17px;
  align-items: center;
  padding: 0 28px 0 0;
  height: 4.5rem;
}
.floatingdiv img {
  transform: scale(0.4);
}
.floatingdiv span {
  font-family: sans-serif;
  font-size: 16px;
  color: black;
}
